.product{
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid grey;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}

.product-browser{
    height: 20px;
    background-color: grey;
    display: flex;
    align-items: center;
    position: sticky;
    z-index: 2;
}

.product-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 3px;
    background-color: white;
}

.product-img {
    width: 100%;
    transition: all 10s ease;
  }
  
  .product:hover .product-img {
    transform: translateY(-100%);
  }

  @media screen and (max-width: 480px) {
    .product {
      width: 40%;
      height: 20vh;
    }
  }