.productlist{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.productlist-texts{
    width: 65%;
}

.productlist-title {
    font-size: 50px;
    font-weight: 600;
  }
  
  .productlist-desc {
    margin: 20px 0px;
  }
  
  .product-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media screen and (max-width: 480px) {
    .productlist {
      padding: 10px;
    }
  
    .productlist-text {
      width: 100%;
    }
  
    .productlist-desc {
      display: none;
    }
  }
